<template>
  <v-container fill-height>
    <v-row class="text-center" align="center" justify="center">
      <v-col cols="12">
        <v-card width="500" class="pa-5 text-center d-inline-block">
          <v-card-title class="px-0">Login</v-card-title>
          <v-form ref="form" v-model="valid">
            <v-text-field
              v-model="email"
              :rules="emailRules"
              label="E-mail"
              required
            ></v-text-field>
            <v-text-field
              v-model="password"
              :append-icon="show4 ? 'mdi-eye' : 'mdi-eye-off'"
              :rules="[rules.required]"
              :type="show4 ? 'text' : 'password'"
              name="input-10-2"
              label="Password"
              hint="At least 8 characters"
              @click:append="show4 = !show4"
            ></v-text-field>

            <v-btn :disabled="!valid" color="success" class="mr-4" @click="send">
              Send
            </v-btn>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "../api/index";
export default {
  name: "Login",
  data: () => ({
    valid: true,
    name: "",
    nameRules: [
      (v) => !!v || "Name is required",
      (v) => (v && v.length <= 10) || "Name must be less than 10 characters",
    ],
    email: "",
    emailRules: [
      (v) => !!v || "E-mail is required",
      (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
    ],
    select: null,
    items: ["Item 1", "Item 2", "Item 3", "Item 4"],
    checkbox: false,
    show4: false,
    password: "",
    rules: {
      required: (value) => !!value || "Required.",
      min: (v) => v.length >= 8 || "Min 8 characters",
    },
  }),
  methods: {
    validate() {
      this.$refs.form.validate();
    },
    reset() {
      this.$refs.form.reset();
    },
    send() {
      axios
        .post("/api/v1/login", {
          email: this.email,
          password: this.password,
        })
        .then((user) => {
          localStorage.setItem("access_token", user.data.token);
          localStorage.setItem("ref", user.data.user.ref);
          this.$router.push({ path: "/" });
        })
        .catch((error) => {
          console.log(error);
          // Nprogress.done();
        });
    },
  },
};
</script>
